import React from "react";
import { motion } from "framer-motion";

const aboutImgFoot = require("../../../Images/foot.png");

const imgFootArray2 = [
  { src: aboutImgFoot, class: "back-img-foot foot4" },
  { src: aboutImgFoot, class: "back-img-foot foot3" },
  { src: aboutImgFoot, class: "back-img-foot foot2" },
  { src: aboutImgFoot, class: "back-img-foot foot1" },
];

export const About = () => {
  const onePageAnimation = {
    hidden: {
      display: "none",
      opacity: 0,
    },

    visible: {
      display: "flex",
      opacity: 1,
      transition: { duration: 0.2, delay: 0.4 },
    },

    exit: {
      opacity: 0,
      transition: { duration: 0.2 },
      transitionEnd: { display: "none" },
    },
  };

  const variAbout = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        when: "beforeChildren",
        staggerChildren: 0.15,
      },
    },
  };

  const variAboutChildren1 = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
    },
  };

  const variAboutChildren2 = {
    hidden: { x: -100, opacity: 0 },
    visible: {
      x: 0,
      opacity: 1,
      transition: { duration: 0.5 },
    },
    exit: {
      x: -100,
      transtion: { duration: 0.2 },
    },
  };

  const variAboutChildren3 = {
    hidden: { x: 100, opacity: 0 },
    visible: {
      x: 0,
      opacity: 1,
      transition: { duration: 0.5, delay: 1.1 },
    },
    exit: {
      x: 200,
      transtion: { duration: 0.2 },
    },
  };

  return (
    <motion.main
      variants={onePageAnimation}
      initial="hidden"
      animate="visible"
      exit="exit"
      className="about-container"
    >
      <motion.section
        variants={variAbout}
        initial="hidden"
        animate="visible"
        exit="exit"
      >
        {imgFootArray2.map((item, index) => {
          return (
            <motion.figure variants={variAboutChildren1} key={index}>
              <img src={item.src} className={item.class} alt="rat" />
            </motion.figure>
          );
        })}
        <motion.figure variants={variAboutChildren1}>
          <img
            src={require("../../../Images/ratAbout1.jpg")}
            className="back-img"
            alt="rat"
          />
        </motion.figure>
        <motion.div
          variants={variAboutChildren2}
          className="div-container-about"
        >
          <article className="about-content">
            <section className="about-text">
              <p>
                Bonjour  tous ! Je m'appelle Alexandra et{" "}
                <span>je suis une grande passione d'animaux en gnrale.</span>{" "}
                J'aime m'en occuper, leur apporter de lamour et avoir une
                complicit avec eux, c'est pourquoi{" "}
                <span>
                  faire mon levage de rats tait pour moi une vidence.
                </span>{" "}
                Pourquoi spcialement les rats ? Pour leurs petites tailles ?
                Pour leurs petits yeux doux ? Pour leur soyeuse petite fourrure
                ? Je ne serais dire exactement ce qui m'attire chez eux, mais
                ils ont russi  prendre mon coeur.
                <br />
                <br /> Ce n'est qu'il y a quelques annes que j'ai dcid de
                prendre des rats. C'est plus tard que je me suis tourner vers
                l'lvage.{" "}
                <span>
                  Mes rats sont leves dans un bon environnement, entour
                  d'autres rats
                </span>{" "}
                il ne manque pas de vivre ni de bien tre, manipuler tous les
                jours et en bonne sant.{" "}
                <span>
                  Je me rserve le droit daccepter ou non votre demande
                  dadoption.
                </span>{" "}
                Je vous donne des conseils si ncessaire. <br />
                Si vous avez besoin de me contacter, il suffit de m'envoyer des
                messages prives sur mon instagram ou par mail dans la catgorie
                "Rseaux".
                <br />
                <br /> Je vous souhaite de trouver{" "}
                <span>votre futur petit compagnon !</span>
              </p>
            </section>
            <motion.section
              variants={variAboutChildren3}
              className="about-parent"
            >
              <h2 className="about-h2">Les Rats reproducteurs</h2>
              <img
                src={require("../../../Images/parent1.jpg")}
                alt="rat"
                className="parent one"
              />
              <img
                src={require("../../../Images/parent2.jpg")}
                alt="rat"
                className="parent two"
              />
            </motion.section>
          </article>
        </motion.div>
      </motion.section>
    </motion.main>
  );
};
